@charset "UTF-8";
html,
body {
  position: relative;
  overflow-x: hidden;
  background: #f5f5f5;
  min-height: 100vh;
  font: 400 17px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}
html.fix,
body.fix {
  overflow: hidden;
}

h1 {
  font: 500 36px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}

h2 {
  font: 500 25px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}

h3 {
  font: 500 18px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}

h4 {
  font: 400 17px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}

h5 {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #383b49;
}

h6 {
  font: 400 12px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}

.container {
  max-width: 1305px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: content-box;
}

a,
button {
  transition: all 0.3s;
}
a:hover,
button:hover {
  opacity: 0.6;
}

.btn__transparent {
  border: 1px solid #fff;
  border-radius: 4px;
  padding: 8px 16px;
  display: block;
  width: max-content;
  box-shadow: 0 12px 10px 0 rgba(0, 0, 0, 0.07);
  font: 600 15px "IBM Plex Sans", sans-serif;
  text-align: center;
  color: #fff;
  background: transparent;
  transition: all 0.3s;
}
.btn__transparent_blue {
  color: #1d3b95;
  border-color: #1d3b95;
}
.btn__blue {
  border-radius: 4px;
  padding: 10px 16px;
  display: block;
  width: max-content;
  box-shadow: 0 12px 10px 0 rgba(0, 0, 0, 0.07);
  background: #7d7aff;
  border: none;
  font: 600 15px "IBM Plex Sans", sans-serif;
  text-align: center;
  color: #fff;
}
.btn__blue:hover {
  color: #e9e9e9;
  opacity: 1;
}

.dropdown {
  position: relative;
}
.dropdown__btn {
  cursor: pointer;
}
.dropdown__list {
  padding: 14px 8px;
  background: #fff;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  width: max-content;
  display: grid;
  grid-template-columns: 1fr;
  gap: 9px;
  z-index: 4;
}
.dropdown__list.active {
  opacity: 1;
  pointer-events: auto;
}
.dropdown__list p {
  color: #1d3b95 !important;
}
.dropdown__list li {
  display: block;
}

input[type=text], input[type=tel], input[type=password], input[type=email] {
  width: 100%;
  box-sizing: border-box;
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #383b49;
  padding: 9px 15px;
  border-radius: 5px;
  background: #fff;
  border: 1px solid rgba(40, 47, 54, 0.15);
}

textarea {
  width: 100%;
  box-sizing: border-box;
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #383b49;
  padding: 9px 15px;
  border-radius: 5px;
  background: #fff;
  border: 1px solid rgba(40, 47, 54, 0.15);
}

span.error {
  color: #d76060;
  font-size: 12px;
  margin: 3px 0 0;
}

.input__password {
  position: relative;
}
.input__password a {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  width: 22px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.input__password a path {
  transition: all 0.3s;
}
.input__password a:hover path {
  fill: #7d7aff;
  fill-opacity: 1;
}

.select {
  position: relative;
  cursor: pointer;
}
.select svg {
  position: absolute;
  right: 12px;
  top: 20px;
  z-index: 2;
}
.select ul {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  border: 1px solid #3c81e8;
  border-radius: 0 0 5px 5px;
  border-top: none;
  background: #fff;
  pointer-events: none;
  opacity: 0;
  z-index: 10;
}
.select ul li {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #686e7d;
  cursor: pointer;
  padding: 14px;
  transition: all 0.3s;
}
.select ul li:hover {
  background: #f5f5f5;
}
.select input {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #383b49;
  padding: 9px 18px;
  border: 1px solid #f5f5f5;
  box-shadow: none;
  border: 1px solid rgba(40, 47, 54, 0.15);
  background: #fff;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
  cursor: pointer;
  z-index: 1;
  position: relative;
}
.select input.active {
  border: 1px solid #3c81e8;
  border-radius: 5px 5px 0 0;
  border-bottom: 1px solid #dce1e9;
}
.select input.active + ul {
  opacity: 1;
  pointer-events: all;
}
.select input.active + ul + svg {
  transform: rotate(180deg);
}

.title {
  padding: 0 38px 8px;
  border-bottom: 1px solid #e9e9e9;
}
.title h2,
.title h1 {
  font: 500 18px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}

.filter_title {
  padding: 0 38px 8px;
  border-bottom: 1px solid #e9e9e9;
  font: 500 18px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}
.filter_title p,
.filter_title h4 {
  font: 500 18px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}

.nav {
  background: url("../img/nav_bg.svg") center no-repeat;
  background-size: cover;
  padding: 22px 15px;
  position: relative;
}
.nav__hum {
  display: none;
}
.nav::before {
  content: "";
  display: block;
  background: #fff;
  opacity: 0.2;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 30px);
  max-width: 1305px;
  bottom: 0;
  height: 1px;
  position: absolute;
}
.nav .container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__logo {
  width: 115px;
  opacity: 1 !important;
}
.nav__list {
  margin: 0 auto;
  gap: 100px;
  width: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav__list a {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  text-align: center;
  color: #fff;
  position: relative;
  transition: all 0.3s;
}
.nav__list a::before {
  content: "";
  display: block;
  position: absolute;
  bottom: -31px;
  left: -15px;
  right: -15px;
  height: 2px;
  background: linear-gradient(90deg, transparent 0%, #fff 50%, transparent 100%);
  opacity: 0;
  transition: all 0.3s;
  pointer-events: none;
}
.nav__list a.active, .nav__list a:hover {
  opacity: 1;
}
.nav__list a.active::before, .nav__list a:hover::before {
  opacity: 1;
}
.nav__lang_row {
  display: grid;
  grid-template-columns: 16px auto 8px;
  align-items: center;
  gap: 9px;
}
.nav__lang_row a {
  display: grid;
  grid-template-columns: 16px auto 8px;
  align-items: center;
  gap: 9px;
}
.nav__lang_row p {
  font: 400 14px "Open Sans", sans-serif;
  color: #fff;
}
.nav__enter {
  margin: 0 35px;
}
.nav__for {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  text-align: center;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 0 0 0 32px;
  position: relative;
  transition: all 0.3s;
}
.nav__for::before {
  content: "";
  display: block;
  height: 36px;
  width: 1px;
  background: #fff;
  opacity: 0.2;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

header .nav,
section .nav {
  background: transparent;
}

.header {
  background-position: top center;
  background-size: cover;
  padding: 0 0 76px;
}
.header__block {
  margin: 60px 0 0;
}
.header h1 {
  font: 500 36px/1.4 "IBM Plex Sans", sans-serif;
  text-align: center;
  color: #fff;
}
.header__subtitle {
  font: 400 17px/1.4 "IBM Plex Sans", sans-serif;
  text-align: center;
  color: #fff;
}
.header__subtitle span {
  font-weight: 500;
}
.header__row {
  margin: 36px auto 0;
  max-width: 853px;
  display: grid;
  grid-template-columns: 41fr 26fr auto;
  gap: 10px;
}
.header__row input {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #383b49;
  padding: 9px 16px;
  border: 1px solid #f5f5f5;
  border-radius: 4px;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.12), inset 0 4px 4px 0 rgba(0, 0, 0, 0.1);
  background: #fff;
  width: 100%;
  box-sizing: border-box;
}
.header__example {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #e9e9e9;
  margin: 9px auto 0;
  max-width: 829px;
}
.header__example a {
  border-bottom: 1px dashed #e9e9e9;
  color: #e9e9e9;
}

.vacancy-row {
  padding: 49px 0 30px;
}
.vacancy-row__row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 9px;
}
.vacancy-row__item {
  border-radius: 5px;
  box-shadow: 0 1px 2px 0 rgba(61, 58, 194, 0.12);
  background: #fff;
  padding: 20px 26px;
  gap: 27px;
  grid-template-columns: 61px 1fr;
  align-items: center;
  display: grid;
}
.vacancy-row__item p {
  font: 400 17px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
  max-width: 160px;
  transition: all 0.3s;
}
.vacancy-row__item:hover {
  opacity: 1;
}
.vacancy-row__item:hover p {
  color: #7d7aff;
}

.publish {
  padding: 0 0 30px;
}
.publish__block {
  box-shadow: 0 1px 2px 0 rgba(61, 58, 194, 0.12);
  background: linear-gradient(144deg, #fff 0%, #ecf4fc 100%);
  border-radius: 5px;
  display: grid;
  grid-template-columns: 500px 1fr;
  gap: 20px;
  align-items: center;
  padding: 0 36px;
}
.publish__prev {
  align-self: flex-end;
}
.publish__prev img {
  width: 100%;
}
.publish__info {
  padding: 20px 0;
}
.publish__info p {
  font: 400 17px/1.4 "IBM Plex Sans", sans-serif;
  color: #686e7d;
  margin: 8px 0 0;
}
.publish__info .btn__blue {
  margin: 31px 0 0;
}
.publish h2 {
  font: 500 25px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}

.popular {
  padding: 0 0 59px;
}
.popular__block {
  border-radius: 5px;
  box-shadow: 0 1px 2px 0 rgba(61, 58, 194, 0.12);
  background: #fff;
  padding: 30px;
}
.popular__block .btn__transparent {
  margin: 40px auto 0;
}
.popular__wrapper {
  margin: 27px auto 0;
  max-width: 1202px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 20px;
}
.popular__item img {
  width: 100%;
  box-shadow: 0 1px 2px 0 rgba(61, 58, 194, 0.12);
  border-radius: 15px;
}
.popular__item p {
  font: 500 13px/1.25 "IBM Plex Sans", sans-serif;
  text-align: center;
  color: #364051;
  margin: 6px 0 0;
}

.vacancy {
  padding: 0 0 45px;
}
.vacancy__wrapper {
  margin: 24px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 14px;
}
.vacancy__item {
  border-radius: 5px;
  box-shadow: 0 1px 2px 0 rgba(61, 58, 194, 0.12);
  background: #fff;
  padding: 15px 25px;
}
.vacancy__item_row {
  display: grid;
  grid-template-columns: 1fr 80px;
  align-items: start;
  gap: 20px;
}
.vacancy__item h3 {
  font: 500 18px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}
.vacancy__item_logo img {
  width: 80px;
  height: 80px;
  border-radius: 3px;
  box-shadow: 0 1px 2px 0 rgba(61, 58, 194, 0.12);
}
.vacancy__item_pro h3::after {
  content: "PRO";
  padding: 1px 10px;
  border-radius: 3px;
  background: #71a862;
  font: 400 12px/1.4 "IBM Plex Sans", sans-serif;
  color: #fff;
  margin: 0 0 0 15px;
}
.vacancy__item_place {
  display: flex !important;
  justify-content: flex-start;
  align-items: center;
  gap: 9px;
}
.vacancy__item_place a {
  font: 400 18px "IBM Plex Sans", sans-serif;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: #969fa9;
}
.vacancy__item_place p {
  font: 400 17px/1.4 "IBM Plex Sans", sans-serif;
  color: #969fa9;
}
.vacancy__item_salary {
  margin: 6px 0 0;
  font: 500 18px/1.4 "IBM Plex Sans", sans-serif;
  color: #7d7aff;
}
.vacancy__item_desc {
  margin: 13px 0 auto;
}
.vacancy__item_desc p {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #686e7d;
}
.vacancy__item_footer {
  margin: 11px 0 0;
  padding: 11px 0 0;
  border-top: 1px solid #e9e9e9;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
}
.vacancy__item_footer p {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #969fa9;
}
.vacancy__item_footer a {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #7d7aff;
}

.sub {
  padding: 0 0 40px;
  color: #fff;
}
.sub h3 {
  color: #fff;
}
.sub__container {
  padding: 28px 60px 44px 555px;
  position: relative;
  box-shadow: 0 12px 20px 0 rgba(61, 58, 194, 0.08);
  background: linear-gradient(148deg, #55408e 0%, #544485 100%);
}
.sub__bg {
  position: absolute;
  bottom: 12px;
  left: -12px;
  width: 525px;
}
.sub h3 {
  font: 700 25px "IBM Plex Sans", sans-serif;
}
.sub__subtitle {
  margin: 9px 0 0;
  font: 400 17px/1.4 "IBM Plex Sans", sans-serif;
}
.sub__row {
  margin: 29px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.sub__row input {
  padding: 10px 16px;
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #383b49;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #f5f5f5;
  border-radius: 4px;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.12), inset 0 4px 4px 0 rgba(0, 0, 0, 0.1);
  background: #fff;
}
.sub__row input::placeholder {
  color: #969fa9;
}
.sub__func {
  margin: 20px 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}
.sub__link {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 9px;
}
.sub__link span {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #fff;
  text-decoration: underline;
}

.article {
  padding: 0 0 90px;
}
.article__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 27px;
}
.article__job, .article__news {
  display: grid;
  grid-template-rows: auto 1fr;
}
.article__block {
  margin: 13px 0 0;
  box-shadow: 0 1px 2px 0 rgba(61, 58, 194, 0.12);
  background: #fff;
  border-radius: 3px;
  padding: 30px;
}
.article__job_text p {
  font: 400 17px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}
.article__news_list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 14px;
}
.article__news_date {
  padding: 3px;
  width: 100%;
  border: 1px solid #1d3b95;
  border-radius: 2px;
  transition: all 0.3s;
  font: 400 16px/1.375 "Roboto", sans-serif;
  text-align: center;
  color: #1d3b95;
  text-align: center;
}
.article__news_info p {
  font: 600 17px/1.4 "IBM Plex Sans", sans-serif;
  color: #383b49;
}
.article__news_info p + p {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #686e7d;
}
.article__news_item {
  padding: 0 0 14px;
  border-bottom: 1px solid #e9e9e9;
  display: grid;
  grid-template-columns: 50px 1fr;
  gap: 20px;
  align-items: start;
  opacity: 1 !important;
}
.article__news_item:last-child {
  padding: 0;
  border-bottom: none;
}
.article__news_item:hover .article__news_date {
  background: #1d3b95;
  color: #fff;
}
.article__news_item:hover .article__news_info p {
  text-decoration: underline;
}
.article__news_func {
  margin: 24px 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
}
.article__news_func a {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #7d7aff;
}
.article__news_func p {
  font: 400 18px "IBM Plex Sans", sans-serif;
  color: #969fa9;
}

.footer {
  padding: 20px 0;
  background: url("../img/footer_bg.jpg") top repeat;
  background-size: contain;
  border-top: 1px solid rgba(29, 59, 149, 0.3019607843);
}
.footer .container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.footer__desc {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #383b49;
  display: none;
}
.footer__desc span {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #283157;
}
.footer__logo a {
  width: 115px;
  display: block;
}
.footer__logo a img {
  width: 100%;
}
.footer__logo .footer__desc {
  display: block;
}
.footer__menu {
  margin: 0 auto;
}
.footer .nav__lang {
  width: max-content;
  margin: 0 0 0 auto;
}
.footer .nav__lang_row p {
  color: #383b49;
}
.footer .nav__for {
  padding: 0;
  color: #1d3b95;
  margin: 8px 0 0;
  gap: 7px;
}
.footer .nav__for path {
  fill: #1d3b95;
}
.footer .nav__for::before {
  content: none;
}
.footer .dropdown__list {
  top: auto;
  bottom: calc(100% + 5px);
  right: 0;
  left: auto;
}
.footer__row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
}
.footer__row a {
  font: 700 15px "Open Sans", sans-serif;
  color: #383b49;
}
.footer__list {
  margin: 6px 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
}
.footer__list a {
  font: 400 15px/1.45 "IBM Plex Sans", sans-serif;
  color: #383b49;
}
.footer__list li {
  position: relative;
}
.footer__list li::before {
  content: "";
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: calc(100% + 8px);
  background: #383b49;
}
.footer__list li:last-child::before {
  content: none;
}

@media (max-width: 1024px) {
  .nav {
    padding: 22px 0;
  }
  .nav .container {
    display: grid;
    grid-template-columns: 1fr 35px;
    gap: 15px;
  }
  .nav__list, .nav__lang, .nav__lang, .nav__for, .nav__enter {
    display: none;
  }
  .nav__logo {
    display: block;
  }
  .nav__hum {
    display: block;
    width: 35px;
    height: 20px;
    position: relative;
    cursor: pointer;
  }
  .nav__hum::before, .nav__hum::after,
  .nav__hum span {
    content: "";
    display: block;
    width: 35px;
    height: 1px;
    position: absolute;
    top: 0;
    left: 0;
    background: #fff;
  }
  .nav__hum::after {
    bottom: 0;
    top: auto;
  }
  .nav__hum span {
    top: 50%;
  }
  .nav.active {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: url("../img/header_bg.svg") center no-repeat;
    background-size: cover;
    z-index: 10;
    align-items: start;
  }
  .nav.active .nav__hum span {
    opacity: 0;
  }
  .nav.active .nav__hum::before, .nav.active .nav__hum::after {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .nav.active .nav__hum::after {
    transform: translate(-50%, -50%) rotate(45deg);
  }
  .nav.active .nav__list, .nav.active .nav__lang, .nav.active .nav__lang, .nav.active .nav__for, .nav.active .nav__enter {
    display: block;
    order: 2;
    grid-column: 1/3;
  }
  .nav.active .nav__list {
    display: grid;
